import React from "react";
import s from './Footer.module.scss'
import styleContainer from '../../common/style/Container.module.css'


export const Footer = () => {
    return (
        <div className={s.footerBlock}>
            <div className={`${styleContainer.container} ${s.containerFooter}`}>
                <h5 className={s.footerHeading}>Исаев Александр</h5>

                <div className={s.linkContainer}>
                    <div className={s.icon}>1</div>
                    <div className={s.icon}>2</div>
                    <div className={s.icon}>3</div>
                    <div className={s.icon}>4</div>
                </div>

                <h6 className={s.footerHeading}>2021 Все права защищены</h6>
            </div>
        </div>
    )
}