import React from "react";
import s from './Main.module.css'
import styleContainer from '../../common/style/Container.module.css'
import PhotoMe from '../../assets/image/Photo.jpg'

type PropsType = {
    toggle: boolean
}

export const Main = (props:PropsType) => {
    return (
        <div className={props.toggle? `${s.mainBlock} ${s.active}` : s.mainBlock}>
            <div className={styleContainer.container}>
                <div className={s.text}>
                    <h5 className={s.heading}>Front-end developer</h5>
                    <h1 className={s.name}><span style={{color:'#00b074'}}>Isaev</span> Alexandr</h1>
                </div>

                <div>
                    <img className={s.photo} src={PhotoMe}/>
                </div>
            </div>
        </div>
    )
}