import React from "react";
import s from './Nav.module.css'

type PropsType = {
    toggle: boolean
}
export const Nav = (props:PropsType) => {
    return(
        <nav className={props.toggle? `${s.navContainer} ${s.active}`:s.navContainer}>

                <ul className={s.list}>
                    <li>
                        <a className={s.navLink} href="">Home</a>
                    </li>
                    <li><a className={s.navLink} href="">Skills</a></li>
                    <li><a className={s.navLink} href="">Portfolio</a></li>
                    <li><a className={s.navLink} href="">Contacts</a></li>
                </ul>
        </nav>
    )
}