import React from "react";
import styleContainer from '../../common/style/Container.module.css'
import s from './MyProjects.module.scss'
import {Project} from "./project/Project";
import {Title} from "../../common/title/Title";
import SocialNetwork from '../../assets/image/SocialNetwork.jpg'
import Todo from '../../assets/image/TodoList.jpg'
import Flower from '../../assets/image/flower.jpg'
import Counter from '../../assets/image/Counter.jpg'

export const MyProjects = () => {
    const social = {
        backgroundImage: `url(${SocialNetwork})`
    };

    const todo = {
        backgroundImage: `url(${Todo})`
    }
    const flower = {
        backgroundImage: `url(${Flower})`
    }

    const counter = {
        backgroundImage: `url(${Counter})`
    }

    return (
        <div className={s.myProjectBlock}>
            <div className={`${styleContainer.container} ${s.projectContainer}`}>
                <Title text={"Project"} color={"Black"}/>
                <div className={s.myProjects}>
                    <Project style={social} title={"Social Network"} description={"DSDdsdsdsdaaaaaaadddddddddddddddaddddddddddddddddddddddddddddddddddddddddddddddddddddddddddddddddddddddddddaaaaaaaaaaaaaaaaaaaaassssssssssssssssssdsd"}/>
                    <Project style={todo} title={"Todolist"} description={"DSDdsdsdsssssssdaaaaaaaaaaaaaaaaaaddddddddddddddddddddddddddddddddddddaaaaaaaaaaaaaaaaaaaaaassssssssssssdsd"}/>
                    <Project style={flower} title={"Flower market"} description={"DSDdsdsdsdaaaaaaaaaaaaaaaaadddddddddddddddddddddddddddddddddddddddaaaaaaaaaaaaaaaaaaaaaaassssssssssssssssssdsd"}/>
                    <Project style={counter} title={"Counter"} description={"DSDdsdsdssssssdaaaaaaaaaaaaaaaaadddddddddddddddddddddssssssssssssaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaasssssssssssssdsd"}/>
                </div>
            </div>
        </div>
    )
}